import type { Data } from '~/types/albums'
import { Layout, Intro, About, Stack, Inspiration, Contact } from '~/ui/components'

interface HomeProps extends Data {
  animateBgColorOnScroll: (container: HTMLElement) => void
  animateAbout: () => void
  animateStack: () => void
  animateAlbums: (Container: HTMLElement) => void
  animateContact: () => void
  createImageInteraction: () => void
}

const Home = ({
  albums,
  animateBgColorOnScroll,
  animateAbout,
  animateStack,
  animateAlbums,
  animateContact,
  createImageInteraction
}: HomeProps) => {
  return (
    <Layout animateBgColorOnScroll={animateBgColorOnScroll}>
      <Intro />
      <About animateAbout={animateAbout} createImageInteraction={createImageInteraction} />
      <Stack animateStack={animateStack} />
      <Inspiration albums={albums} animateAlbums={animateAlbums} />
      <Contact animateContact={animateContact} />
    </Layout>
  )
}

export { Home }
