import { useEffect } from 'react'
import { Container } from '~/ui/primitives'
import Email from './Email'
import Social from './Social'

interface ContactProps {
  animateContact: () => void
}

export function Contact({ animateContact }: ContactProps) {
  useEffect(() => {
    animateContact()
  }, [animateContact])

  return (
    <Container as="section" paddingTop={{ xs: 26, lg: 0 }} paddingBottom={{ xs: 40, lg: 100 }}>
      <Container variant="wrap" maxHeight={708}>
        <Email />
        <Social />
      </Container>
    </Container>
  )
}
