import { useEffect, useCallback, useMemo, useRef } from 'react'
import gsap from 'gsap'
import {
  animateBgColorOnScroll,
  animateAbout,
  animateStack,
  animateAlbums,
  animateContact
} from '~/utils/animations'
import { useIsMounted } from './useIsMounted'
import { useWindowSize } from './useWindowSize'

export function useAnimations() {
  const ScrollTrigger = useRef(null)
  const Draggable = useRef(null)

  const isMounted = useIsMounted()
  const { width } = useWindowSize()

  const screenWidths = useMemo(
    () => ({
      phoneOnly: width < 480,
      phoneUp: width >= 480,
      laptopUp: width >= 1024,
      desktopUp: width >= 1280,
      bigDesktopUp: width >= 1536
    }),
    [width]
  )

  useEffect(() => {
    ScrollTrigger.current = require('gsap/ScrollTrigger')
    Draggable.current = require('gsap/Draggable').Draggable
    const plugins = [ScrollTrigger, Draggable]

    plugins.forEach((plugin) => {
      if (plugin?.current) {
        gsap.registerPlugin(plugin.current)
      }
    })
  }, [isMounted])

  const animations = {
    animateBgColorOnScroll: useCallback(
      (container: HTMLElement) => {
        if (isMounted) {
          animateBgColorOnScroll(gsap, container)
        }
      },
      [isMounted]
    ),
    animateAbout: useCallback(() => {
      if (isMounted) {
        animateAbout(gsap, screenWidths)
      }
    }, [isMounted, screenWidths]),
    animateStack: useCallback(() => {
      if (isMounted) {
        animateStack(gsap)
      }
    }, [isMounted]),
    animateAlbums: useCallback(
      (container: HTMLElement) => {
        if (isMounted && Draggable.current) {
          animateAlbums(gsap, container, width, screenWidths, Draggable.current)
        }
      },
      [isMounted, width, screenWidths]
    ),
    animateContact: useCallback(() => {
      if (isMounted) {
        animateContact(gsap)
      }
    }, [isMounted])
  }

  return animations
}
