import { useEffect } from 'react'
import { Container } from '~/ui/primitives'
import FirstRow from './FirstRow'
import SecondRow from './SecondRow'
import ThirdRow from './ThirdRow'

interface AboutProps {
  animateAbout: () => void
  createImageInteraction: () => void
}

export function About({ animateAbout, createImageInteraction }: AboutProps) {
  useEffect(() => {
    animateAbout()
    createImageInteraction()
  }, [animateAbout, createImageInteraction])

  return (
    <Container
      as="section"
      id="about"
      paddingTop={{ xs: 46, lg: 86 }}
      paddingBottom={86}
      minHeight="screen"
    >
      <Container variant="wrap" height="100%">
        <FirstRow />
        <SecondRow />
        <ThirdRow />
      </Container>
    </Container>
  )
}
