const selectors = {
  container: '#stack',
  skelly: '.skelly'
}

export function animateStack(gsap: GSAP) {
  const stackTl = gsap.timeline({
    scrollTrigger: {
      scrub: true,
      trigger: selectors.container,
      start: 'top 70%'
    }
  })
  stackTl.to(
    selectors.skelly,
    {
      y: -180,
      ease: 'power1.inOut'
    },
    0
  )
}
